(function () {
  'use strict';

  angular.module('app.room').config(route);

  route.$inject = ['$stateProvider'];

  function route($stateProvider) {
    $stateProvider
      .state('room', {
        url: '/rooms/:roomId',
        controller: 'RoomController',
        controllerAs: 'room',
        templateUrl: 'app/room/room.html',
        resolve: {
          currentAuth: ['auth', function(auth) {
            return auth.$requireAuth();
          }]
        }
      });
  }
})();
